import TouchAppRoundedIcon from '@mui/icons-material/TouchAppRounded';
import {Box} from '@mui/material';
import type {ReactElement} from 'react';
import React from 'react';

import {typography} from '../../styles/typography';
import type {Task} from '../Pages/assignments';
import {useEventListener} from '../Pages/useEventListener';
import {styles} from './styles';

interface StartProps {
    onNext: () => void;
    tasks: Task[];
}

export function SessionStart({onNext, tasks}: StartProps): ReactElement {
    useEventListener('keydown', event => onNext());

    return (
        <div
            className={`${styles.fullScreen} ${styles.withDarkerBackground}`}
            onClick={onNext}
        >
            <div className={styles.centered}>
                <div>
                    <p>
                        Put on your blindfold and press any key or click
                        anywhere to start the exercise.
                    </p>
                    <p>Screen:</p>
                    {tasks.map(task => (
                        <Box
                            key={task.id}
                            display="flex"
                            alignItems="stretch"
                            justifyContent="center"
                            width="300px"
                            height="180px"
                            borderRadius="5px"
                            mb={1}
                            marginLeft="auto"
                            marginRight="auto"
                            sx={{backgroundColor: task.color}}
                        >
                            {tasks.map(t => (
                                <Box
                                    key={t.id}
                                    flexDirection="column"
                                    flex={1}
                                    display="flex"
                                    alignItems="center"
                                    justifyContent="center"
                                >
                                    {task.id === t.id && (
                                        <>
                                            <span
                                                className={
                                                    typography.contrastingText
                                                }
                                            >
                                                {t.label}
                                            </span>
                                            <TouchAppRoundedIcon fontSize="small" />
                                        </>
                                    )}
                                </Box>
                            ))}
                        </Box>
                    ))}

                    <p>Keyboard:</p>

                    <ol>
                        {tasks.map(task => (
                            <li key={task.id}>{task.label}</li>
                        ))}
                    </ol>
                </div>
            </div>
        </div>
    );
}
