import {Star} from '@mui/icons-material';
import CheckIcon from '@mui/icons-material/Check';
import ClearIcon from '@mui/icons-material/Clear';
import {
    Box,
    Button,
    Paper,
    Stack,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableFooter,
    TableHead,
    TableRow,
} from '@mui/material';
import type {ReactElement} from 'react';
import React, {useEffect, useMemo} from 'react';
import {Link} from 'react-router-dom';

import {useLocalStorage} from '../../utility/useLocalStorage';
import {PageLayout} from '../Styled/PageLayout';
import {styles} from './styles';
import type {SessionRecord} from './types';

interface StatisticsProps {
    lessonId?: string;
    session: SessionRecord;
}

function usePersistSession(
    lessonId: string | undefined,
    session: SessionRecord,
) {
    const [, setSessionStorage] = useLocalStorage(`results-${lessonId}`);

    useEffect(() => {
        if (lessonId !== undefined) {
            console.log('Persisting session #' + lessonId, session);
            setSessionStorage(session);
        }
    }, [lessonId, session, setSessionStorage]);
}

export function SessionEnd({session, lessonId}: StatisticsProps): ReactElement {
    usePersistSession(lessonId, session);

    const {summary, answers} = session;

    const {totalTasks, totalCorrect} = useMemo(() => {
        let correct = 0;
        let wrong = 0;

        for (const s of Object.values(summary)) {
            correct += s.correct;
            wrong += s.wrong;
        }

        return {
            totalTasks: correct + wrong,
            totalCorrect: correct,
        };
    }, [summary]);

    return (
        <PageLayout>
            <Stack gap={2}>
                <Paper
                    sx={{
                        p: 1,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                    }}
                >
                    {Array(session.score)
                        .fill('')
                        .map((_, i) => (
                            <Star key={i} />
                        ))}
                </Paper>
                <TableContainer component={Paper}>
                    <Table>
                        <TableHead>
                            <TableRow>
                                <TableCell component="th">Task</TableCell>
                                <TableCell component="th" align="right">
                                    Correct
                                </TableCell>
                                <TableCell component="th" align="right">
                                    Wrong
                                </TableCell>
                                {/* <TableCell>Wrong</TableCell> */}
                                <TableCell component="th" align="right">
                                    %
                                </TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {Object.values(summary).map(e => (
                                <TableRow key={e.task.id}>
                                    <TableCell>
                                        {e.task.label}
                                        &nbsp;
                                        {e.task.color && (
                                            <div
                                                className={styles.coloredBlock}
                                                style={{
                                                    backgroundColor:
                                                        e.task.color,
                                                }}
                                            />
                                        )}
                                    </TableCell>
                                    <TableCell align="right">
                                        {[...Array(e.correct)].map((x, i) => (
                                            <CheckIcon
                                                key={i}
                                                color="success"
                                                fontSize="small"
                                            />
                                        ))}
                                    </TableCell>
                                    <TableCell align="right">
                                        {[...Array(e.wrong)].map((x, i) => (
                                            <ClearIcon
                                                key={i}
                                                color="success"
                                                fontSize="small"
                                            />
                                        ))}
                                    </TableCell>
                                    <TableCell align="right">
                                        {e.correct + e.wrong === 0
                                            ? 0
                                            : (
                                                  (e.correct /
                                                      (e.correct + e.wrong)) *
                                                  100
                                              ).toFixed()}
                                        %
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                        <TableFooter>
                            <TableRow>
                                <TableCell>Total</TableCell>
                                <TableCell align="right">
                                    {totalCorrect}
                                </TableCell>
                                <TableCell align="right">
                                    {totalTasks - totalCorrect}
                                </TableCell>
                                <TableCell align="right">
                                    {totalTasks === 0
                                        ? 0
                                        : (
                                              (totalCorrect / totalTasks) *
                                              100
                                          ).toFixed()}
                                    %
                                </TableCell>
                            </TableRow>
                        </TableFooter>
                    </Table>
                </TableContainer>
                <Paper sx={{p: 1}}>
                    <Stack direction="column" gap={1} p={1} mb={2}>
                        {answers.map((answer, i) => (
                            <Box key={i}>
                                <Stack gap={1} direction="row" key={i}>
                                    {answer.correct ? (
                                        <CheckIcon color="success" />
                                    ) : (
                                        <ClearIcon color="success" />
                                    )}
                                    <Box>
                                        {(
                                            (answer.end.getTime() -
                                                answer.start.getTime()) /
                                            1000
                                        ).toFixed(1)}
                                        s
                                    </Box>
                                </Stack>
                            </Box>
                        ))}
                    </Stack>
                    <Button
                        component={Link}
                        to="/blindfold"
                        variant="contained"
                    >
                        Back to learning!
                    </Button>
                </Paper>
            </Stack>
        </PageLayout>
    );
}
