import {CssBaseline, ThemeProvider} from '@mui/material';
import type {PropsWithChildren} from 'react';
import React from 'react';
import {HashRouter, Redirect, Route, Switch} from 'react-router-dom';

import './App.css';
import {BlindfoldCustomPage} from './Components/Pages/Blindfold/BlindfoldCustomPage/BlindfoldCustomPage';
import {BlindfoldIntroPage} from './Components/Pages/Blindfold/BlindfoldIntroPage/BlindfoldIntroPage';
import {BlindfoldLearningPathPage} from './Components/Pages/Blindfold/BlindfoldLearningPath/BlindfoldLearningPathPage';
import {BlindfoldSessionPage} from './Components/Pages/Blindfold/BlindfoldSessionPage/BlindfoldSessionPage';
import {HomePage} from './Components/Pages/HomePage/HomePage';
import {IndexPage} from './Components/Pages/IndexPage/IndexPage';
import {LoginPage} from './Components/Pages/LoginPage/LoginPage';
import {SignUpPage} from './Components/Pages/SignUpPage/SignUpPage';
import {Brain} from './Components/Sandbox/Brain';
import {CloudPage} from './Components/Sandbox/CloudPage';
import {theme} from './styles/theme';
import {ProvideAuth, useAuth} from './useAuth';

export function App() {
    return (
        <ThemeProvider theme={theme}>
            <CssBaseline />
            <ProvideAuth>
                <HashRouter basename={process.env.PUBLIC_URL}>
                    <Switch>
                        <Route path="/" exact>
                            <IndexPage />
                        </Route>
                        <Route path="/home">
                            <HomePage />
                        </Route>
                        <Route path="/login">
                            <LoginPage />
                        </Route>
                        <Route path="/blindfold/session">
                            <BlindfoldSessionPage />
                        </Route>
                        <Route path="/blindfold/intro">
                            <BlindfoldIntroPage />
                        </Route>
                        <Route path="/blindfold" exact>
                            <BlindfoldLearningPathPage />
                        </Route>

                        <Route path="/custom">
                            <BlindfoldCustomPage />
                        </Route>

                        <Route path="/signup">
                            <SignUpPage />
                        </Route>

                        {/* sandbox */}

                        <Route path="/brain">
                            <Brain />
                        </Route>
                        <Route path="/cloud">
                            <CloudPage />
                        </Route>
                    </Switch>
                </HashRouter>
            </ProvideAuth>
        </ThemeProvider>
    );
}

// A wrapper for <Route> that redirects to the login

// screen if you're not yet authenticated.
// eslint-disable-next-line @typescript-eslint/no-unused-vars
function PrivateRoute({children, ...rest}: PropsWithChildren<{}>) {
    const auth = useAuth();
    return (
        <Route
            {...rest}
            render={({location}) =>
                auth.user ? (
                    children
                ) : (
                    <Redirect
                        to={{
                            pathname: '/login',
                            state: {from: location},
                        }}
                    />
                )
            }
        />
    );
}
