import {Box, ButtonBase, Grid, Stack} from '@mui/material';
import type {ReactElement} from 'react';
import React, {useMemo} from 'react';
import {Link} from 'react-router-dom';

import type {SessionRecord} from '../../../BlindfoldSession/types';
import {PageLayout} from '../../../Styled/PageLayout';

interface SessionButtonProps {
    label: string;
    lessonId: string;
}

function SessionButton({label, lessonId}: SessionButtonProps): ReactElement {
    const record = useMemo<SessionRecord | undefined>(() => {
        const json = localStorage.getItem(`results-${lessonId}`);
        return json ? JSON.parse(json) : undefined;
    }, [lessonId]);

    return (
        <ButtonBase
            component={Link}
            to={`/blindfold/session?id=${lessonId}`}
            sx={{
                backgroundColor: 'white',
                borderRadius: '100%',
                border: '1px solid',
                borderColor: 'primary.main',
                width: '5rem',
                height: '5rem',
                fontSize: '1.5rem',
                transition: 'all .3s ease-out',
                ':hover': {
                    transform: 'scale(1.1)',
                },
            }}
        >
            {Array(record?.score ?? 0)
                .fill(null)
                .map((_, i) => (
                    <img
                        src="star-2768.svg"
                        key={i}
                        style={{
                            width: '1.3rem',
                            height: '1.3rem',
                            position: 'absolute',
                            transform: 'translate(-50%, -50%)',
                            ...getStarCoordinates(i),
                        }}
                    />
                ))}
            {label}
        </ButtonBase>
    );
}

function TestButton({label, lessonId}: SessionButtonProps): ReactElement {
    return (
        <ButtonBase
            component={Link}
            to={`/blindfold/session?id=${lessonId}`}
            sx={{
                backgroundColor: 'primary.main',
                color: 'primary.contrastText',
                width: '8rem',
                height: '8rem',
                borderRadius: '100%',
                border: '1px solid',
                borderColor: 'primary.main',
                fontSize: '2rem',
                transition: 'all .3s ease-out',
                ':hover': {
                    transform: 'scale(1.1)',
                },
            }}
        >
            {label}
        </ButtonBase>
    );
}

