function getStarCoordinates(i: number) {
    return {
        top: `${50 - Math.sin(((i * 72 + 90) / 180) * Math.PI) * 30}%`,
        left: `${50 - Math.cos(((i * 72 + 90) / 180) * Math.PI) * 30}%`,
    };
}
